import { FC } from "react";
import {
    Card,
    CardHeader,
    CardBody,
    CardFooter,
    Row,
    Col,
    ApexDonutChart,
    SectionTitle,
} from "@doar/components";
import { useAppSelector } from "../../../redux/hooks";
import {
    StyledChart,
    StyledFooterText,
    StyledFooterDiv,
    StyledFooterBullet,
    StyledFooterTitle,
} from "../../dashboard-two/sessions-channel/style";

const OrderStatistics: FC = () => {
    const { theme } = useAppSelector((state) => state.ui);
    const chartOptions = data.options;
    const darkChartOptions = {
        ...chartOptions,
        stroke: {
            colors: ["#1c273c"],
        },
    };
    return (
        <Card>
            <CardHeader>
                <SectionTitle title="Statistics by order status" />
            </CardHeader>
            <CardBody p={[null, null, null, "25px"]}>
                <StyledChart>
                    <ApexDonutChart
                        options={
                            theme !== "dark" ? chartOptions : darkChartOptions
                        }
                        series={data.series}
                        width="100%"
                        height="100%"
                    />
                </StyledChart>
            </CardBody>
            <CardFooter pb="10px" pt='10px'>
                <Row>
                    {chartOptions.labels.map((one:any,ind)=> <Col col={6}>
                        <StyledFooterText>{one}</StyledFooterText>
                        <StyledFooterDiv>
                            <StyledFooterBullet bg={chartOptions.colors[ind]} />
                            <StyledFooterTitle>
                                <small>{data.series[ind]}%</small>
                            </StyledFooterTitle>
                        </StyledFooterDiv>
                    </Col>)}
                </Row>
            </CardFooter>
        </Card>
    );
};

export default OrderStatistics;


const data = {
    options: {
        labels: ["Ordered", "Canceled","Buyed",'In cart', "Processing", 'Done','Delivering'],
        dataLabels: {
            enabled: false,
        },
        legend: {
            show: false,
        },
        plotOptions: {
            pie: {
                donut: {
                    size: "50%",
                },
            },
        },
        colors: [
            '#ffacac',
            '#0168fa',
            "#7ebcff",
            "#f77eb9",
            "#fdbd88",
            "#7ee5e5",
            '#f10075',

        ],
        stroke: {
            colors: ["#fff"],
        },
    },
    series: [10, 15,45, 10, 10,10,5,],
};
