import { FC } from "react";
import {
    Card,
    CardHeader,
    CardBody,
    CardFooter,
    Row,
    Col,
    ApexDonutChart,
    SectionTitle,
} from "@doar/components";
import { sessionsChart } from "@doar/shared/data/dashboard-two";
import { useAppSelector } from "../../../redux/hooks";
import {
    StyledChart,
    StyledFooterText,
    StyledFooterDiv,
    StyledFooterBullet,
    StyledFooterTitle,
} from "./style";

const SessoionsChannel: FC = () => {
    const { theme } = useAppSelector((state) => state.ui);
    const chartOptions = sessionsChart.options;
    const darkChartOptions = {
        ...chartOptions,
        stroke: {
            colors: ["#1c273c"],
        },
    };
    return (
        <Card>
            <CardHeader>
                <SectionTitle title="Statistics by product status" />
            </CardHeader>
            <CardBody p={[null, null, null, "25px"]}>
                <StyledChart>
                    <ApexDonutChart
                        options={
                            theme !== "dark" ? chartOptions : darkChartOptions
                        }
                        series={sessionsChart.series}
                        width="100%"
                        height="100%"
                    />
                </StyledChart>
            </CardBody>
            <CardFooter pb="10px" pt='10px'>
                <Row>
                    {chartOptions.labels.map((one:any,ind)=> <Col col={6}>
                        <StyledFooterText>{one}</StyledFooterText>
                        <StyledFooterDiv>
                            <StyledFooterBullet bg={chartOptions.colors[ind]} />
                            <StyledFooterTitle>
                                 <small>{sessionsChart.series[ind]}%</small>
                            </StyledFooterTitle>
                        </StyledFooterDiv>
                    </Col>)}
                </Row>
            </CardFooter>
        </Card>
    );
};

export default SessoionsChannel;
